import '../css/components.css';
import varimg from '../img/ad.jpg';

export default function LiveAnyWhere() {
    return (

        <>

            {/* desktop version  */}
            <div className="container">
                <div className="heading">Live Any Where</div>

                <div className="LiveAnyWhereCont">

                    {/* map iterator here */}
                    <div className="LiveAnyWhereSin">
                        <img src={varimg} alt="" />
                        <p>Host your home</p>
                    </div>
                    <div className="LiveAnyWhereSin">
                        <img src={varimg} alt="" />
                        <p>Host an online experience</p>
                    </div>
                    <div className="LiveAnyWhereSin">
                        <img src={varimg} alt="" />
                        <p>Host an Experiener</p>
                    </div>
                    <div className="LiveAnyWhereSin">
                        <img src={varimg} alt="" />
                        <p>London</p>
                    </div>

                </div>

            </div>

        </>

    );
}