import '../css/header.css';
import {useState} from 'react';
import varimg1 from '../img/mail.png';
import varimg2 from '../img/facebook.png';
import varimg3 from '../img/google.png';
import varimg4 from '../img/apple.png';

export default function Header() {

    const [sideBar, setSideBar] = useState(false);
    const [signupPage, setSignupPage] = useState(false);

    const openSignPage = () => {
        setSignupPage(true)
    }

    return (

        <>

            {/* desktop version  */}
            <div className="headUpTxt">Get the Latest on our COVID-19 response</div>
            <div className="headBody">

                <div className="headUpNavMain">

                    <div className="headUpNav">
                        <a className="headUpNavLink" href={"@"}>switch to hosting</a>
                        <span onMouseEnter={() => setSideBar(true)} onMouseLeave={() => setSideBar(false)}>Side bar</span>
                        
                        {sideBar && (
                            <div className="headSideBar" onMouseEnter={() => setSideBar(true)} onMouseLeave={() => setSideBar(false)}>
                                <p onClick={openSignPage}>Sign Up</p>
                                <p>Login</p><hr/>
                                <p>Host your Home</p>
                                <p>Host an experience</p>
                                <p>Help</p>
                            </div>
                        )}

                    </div>
                    <div className="headUpNavTitle">Places to stay</div>

                </div>


                <div className="headNav">

                    <div className="headNav0">
                        <div className="headNav01">
                            <div className="headNav011">Location</div>
                            <div className="headNav012">Where are you going?</div>
                        </div>
                        <div className="headNav01">
                            <center><div className="headNav011">Check in</div></center>
                            <center><div className="headNav012">Adds dates</div></center>
                        </div>
                        <div className="headNav01">
                            <center><div className="headNav011">Check out</div></center>
                            <center><div className="headNav012">Adds dates</div></center>
                        </div>
                        <div className="headNav0111">
                            <center><div className="headNav011">Guests</div></center>
                            <center><div className="headNav012">Add guests</div></center>
                        </div>
                    </div>
                    <div className="headNav1">Icon</div>

                </div>

                <div className="headBodyExplore">
                    <div className="headBodyTxt"><p>Go</p><p>Near</p></div>
                    <div className="headBodyExploreBtn">Explore nearby stays</div>
                </div>

            </div>

            {signupPage && (
                <div className="signupPage">
                    <div className="sign0">
                        <div className="sign01">
                            <p className="sign011">Sign Up</p>
                            <p className="sign012">Look Like you</p>
                        </div>
                        <div className="sign02">
                            <div className="Closebtn">
                                <div onClick={() => setSignupPage(false)}>Close</div>
                            </div>

                            <div className="sign021">
                                <input type="text" placeholder="Enter your name ..." />
                                <span>Icon</span>
                            </div>

                            <div className="sign021">
                                <input type="text" placeholder="Enter your contact no. ..." />
                                <span>Icon</span>
                            </div>

                            <button>Continue</button>

                            <h4>or Continue with</h4>

                            <div className="sign022">
                                <div className="sign0221">
                                    <img src={varimg1} alt="" />
                                    <p><center>Email</center></p>
                                </div>
                                <div className="sign0221">
                                    <img src={varimg2} alt="" />
                                    <p><center>Facebook</center></p>
                                </div>
                                <div className="sign0221">
                                    <img src={varimg3} alt="" />
                                    <p><center>Google</center></p>
                                </div>
                                <div className="sign0221">
                                    <img src={varimg4} alt="" />
                                    <p><center>Apple</center></p>
                                </div>
                            </div>

                            <h5>Already have an account? Login</h5>

                        </div>
                    </div>
                </div>
            )}

        </>

    );
}